@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #393b44;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  align-items: center;
  flex-direction: column;
}

* {
  margin: 0;
  padding: 0;
}

.page-header {
    display: flex; 
    flex-direction: row;
    justify-content: flex-end;
    position: fixed;
    top: 25px;
    right: 10px;
    z-index: 101;
    width: 100%;
  }

.button {
  background-color: #eeeeee;
  box-shadow: 0 5px 0 #212121;
  color: #393b44;
  padding: 1em 1.5em;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
}

.button:hover {
  background-color: #c9c9c9;
}

.button:active {
  box-shadow: none;
  top: 5px;
}
.contentPortfolio {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.containerPortfolio {
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.title-main {
    padding-top: 70px;
    text-align: center;
    color: #f1f3f8;
    font-weight: 700;
    font-size: 2.5rem;
}
.thumbnail {
  position: relative;
  height: 226px;
  width: 384px;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.8);
  overflow: hidden;
  cursor: pointer;
  transition: box-shadow 0.35s ease;
  margin: 20px;
  /* Hover transition */
}
.thumbnail:before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 100%;
}
.thumbnail:hover {
  box-shadow: 0 20px 15px -8px rgba(0, 0, 0, 0.8);
}

.thumbnail__container {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.thumbnail__img {
  flex: 4 0 auto;
  background-position: center;
  background-size: cover;
  transform: scale(1.2);
  transition: transform 0.35s ease;
  /* Hover transition */
}
.thumbnail:hover .thumbnail__img {
  transform: scale(1);
}
.thumbnail__img:before, .thumbnail__img:after {
  position: absolute;
}
.thumbnail__img:before {
  content: "Click";
  top: 50%;
  left: 50%;
  z-index: 10;
  color: #F8F8F8;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 5px;
  line-height: 2;
  text-transform: uppercase;
  background-image: linear-gradient(to right, #F8F8F8 0%, #F8F8F8 100%);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 45% 3%;
  transform: translate(-50%, -50%);
  transition: opacity 0.35s linear, transform 0.35s ease;
  /* Hover transition */
}
.thumbnail:hover .thumbnail__img:before {
  opacity: 0;
  transform: translate(-50%, -500%) scale(0.5);
}
.thumbnail__img:after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: .3;
  transition: opacity 0.35s linear;
  /* Hover transition */
}
.thumbnail:hover .thumbnail__img:after {
  opacity: 0;
}

.thumbnail__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0;
  background-color: #F8F8F8;
  overflow: hidden;
  transition: flex-grow 0.35s ease;
  /* Hover transition */
}
.thumbnail:hover .thumbnail__content {
  flex-grow: 1;
}

.thumbnail__caption {
  color: #393b44;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  overflow: hidden;
  opacity: 0;
  transform: scale(0.5) translateY(100%);
  transition: opacity 0.35s linear, transform 0.35s ease;
  /* Hover transition */
}
.thumbnail:hover .thumbnail__caption {
  transform: scale(1);
  opacity: 1;
}


.contentAbout {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    color: #f1f3f8;
}
.containerProjectView {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #f1f3f8;
    width: 60%;
    padding-bottom: 20px;
}

.info {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
    margin-bottom: 20px;
}

.description {
  margin: 0;
  align-self: center;
}

.box-fill {
  display: inline-block;
  padding: 8px 18px;
  text-align: center;
  text-decoration: none;
  color:#f1f3f8;
  position: relative;
  overflow: hidden;
  background: #393b44;
  transition: color 0.35s ease-in-out;
  z-index: 1;
  border-radius: 0px;
}
.box-fill:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f1f3f8;
  transition: transform 0.35s ease-in-out, opacity 0.35s ease-in-out;
  transform: scale(0);
  z-index: -1;
  opacity: 0;
}
.box-fill:hover {
  color: #393b44;
}
.box-fill:hover:before {
  transform: scale(1);
  opacity: 1;
}

ul{
  list-style-type: circle;
}

.button-small {
  background-color: #eeeeee;
  box-shadow: 0 5px 0 #212121;
  padding: 0.6em 0.6em;
  color: #393b44;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
}

.button-small:hover {
  background-color: #c9c9c9;
}

.button-small:active {
  box-shadow: none;
  top: 5px;
}

hr {
  border: 0; 
  height: 2px; 
  background-image: linear-gradient(to right, #d6e0f0, #8d93ab, #d6e0f0, #d6e0f0);
  width: 40%;
  margin: 10px;
}

.section {
  width: 100%;
}


.title {
  padding-top: 50px;
  color: #f1f3f8;
  font-weight: 700;
  margin: 10px;
}

ul {
  padding-left: 30px;
}

p {
  padding-left: 10px;
}
